.footer__item h3 {
  font-size: 20px;
  font-style: normal;
  font-weight: 700;
  line-height: 120%;
  padding-bottom: 16px;
  color: #ffffff;
}
.footer__item ul {
  padding-left: 0;
}
.footer__item li {
  padding-top: 8px;
}
.footer__item a {
  display: block;
  color: #FFFFFD;
  font-size: 16px;
  line-height: 160%;
  transition: color 0.3s;
}
.footer__item a:hover {
  color: #D3F85A;
}
.footer__rights {
  padding-top: 48px;
}
.footer__rights p {
  font-size: 16px;
  line-height: 160%;
}
.footer__rights a {
  display: block;
  color: #FFFFFD;
  font-size: 16px;
  line-height: 160%;
  margin-top: 16px;
  transition: color 0.3s;
}
.footer__rights a:hover {
  color: #D3F85A;
}

@media (min-width: 768px) {
  .footer__main {
    margin: 0 18px;
  }
  .footer__box {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-bottom: 60px;
  }
  .footer__box p {
    max-width: none;
    text-align: center;
  }
  .footer .logo {
    margin-bottom: 28px;
  }
  .footer__list {
    column-gap: 94px;
    padding-bottom: 0;
  }
  .footer__rights a {
    margin-top: 0;
    margin-left: 28px;
  }
}
@media (min-width: 1100px) {
  .footer {
    padding: 92px 0;
  }
  .footer__main {
    display: flex;
    justify-content: space-between;
    padding-bottom: 48px;
  }
  .footer__box {
    display: block;
    padding-bottom: 0;
  }
  .footer__box p {
    max-width: 272px;
    text-align: start;
  }
  .footer__link-list {
    padding-top: 24px;
  }
  .footer__item {
    padding-bottom: 0;
  }
}